* {
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
}
body {
  background-color: #209fff;
}
.header h1 {
  line-height: 1.5;
  font-size: 40px;
  padding-top: 20px;
  font-weight: 800;
  color: #209fff;
}

.app {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
  background-color: #fdda25;
  color: #209fff;
  border-radius: 8px;
  min-height: 98vh;
  padding: 5px;
}

.app .item-todo {
  padding: 14px;
  width: 600px;
  margin: 20px auto;
  text-align: left;
  background: #209fff;
  color: #fff;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.5s;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app .item-todo .item-text {
  display: inline-block;
  cursor: pointer;
  padding: 5px;
}

.app input {
  padding: 14px 32px 14px 16px;
  border-radius: 8px 0 0 8px;
  border: 2px solid #209fff;
  outline: none;
  width: 500px;
  background: transparent;
  color: #209fff;
  font-weight: bold;
  font-size: 18px;
}

.app input::placeholder {
  color: #209fff;
  font-weight: 700;
  font-size: 18px;
  font-weight: 800;
}

.app button {
  padding: 16px 7px;
  border: none;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
  outline: none;
  background: #209fff;
  color: #fff;
  text-transform: capitalize;
  width: 100px;
  font-size: 18px;
  font-weight: bold;
}

.app .item-todo .item-delete {
  display: block;
  cursor: pointer;
  font-size: 20px;
  position: relative;
  left: -4px;
  align-items: center;
  padding-left: 10px;
}
.strike {
  text-decoration: line-through;
  text-decoration-color: #000;
  text-decoration-thickness: 3px;
}

@media (max-width: 660px) {
  .app .item-todo,
  .app {
    max-width: 100%;
    width: 100%;
  }
  .app input,
  .app button {
    max-width: 100%;
    width: 100%;
    border-radius: 8px;
    margin-top: 10px;
  }
}

@media (max-width: 480px) {
  .header h1 {
    font-size: 26px;
  }
  .app .item-todo {
    padding: 0px 2px 2px;
    font-size: 12px;
  }
  .app .item-todo .item-delete {
    display: block;
    cursor: pointer;
    font-size: 16px;
    position: relative;
    align-items: center;
    padding-left: 4px;
  }
}
